.appeal {
    padding: 165px 0 50px;
    background: #F2F2F2;
    position: relative;

    &__container {
        display: grid;
        grid-template-columns: 1fr 2fr;
        gap: var(--gap);
    }

    &__left {
        grid-row: 1 / 4;
        position: relative;
        img {
            width: 100%;
            height: 100%;
        }
        margin-bottom: 91px;
    }

    &__right {
        display: flex;
        flex-direction: column;
        gap: var(--gap);
    }

    &__cross {
        top: 0;
        transform: translateY(-50%);
        left: 30px;
    }

    &__play {
        width: 183px;
        height: 183px;
        position: absolute;
        bottom: 0;
        right: 30px;
        transform: translateY(50%);

        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            position: relative;
            background: white;
            height: 100%;
            z-index: 2;
            
        }
        
    }
    
    &__right {
        height: 100%;
        justify-content: space-between;
        &-center {
            padding: 20px 80px;
            position: relative;
            margin-bottom: auto;
            grid-column: 2;
            grid-row: 2;
            
            &::before {
                content: '';
                width: 54px;
                height: 44px;
                display: block;
                background: url('../img/svg/quotes.svg');
                position: absolute;
                top: 0;
                left: 0;
            }
            
            &::after {
                content: '';
                width: 54px;
                height: 44px;
                display: block;
                background: url('../img/svg/quotes.svg');
                position: absolute;
                bottom: 0;
                right: 0;
                transform: rotate(180deg);
            }
        }

        @media screen and (max-width: 700px) {
            &-center {
                padding: 0;
        
                &::before {
                    display: none;
                }
        
                &::after {
                    display: none;
                }
            }
            
        }
    }

    &__half-header {
        grid-column: 2;
        // grid-row: 2;
    }

    &__title {
        white-space: pre-wrap;
    }
    
    &__footer {
        grid-column: 2;
        grid-row: 3;
    }

    @media screen and (max-width: 1300px) {
        &__container {
            grid-template-columns: 1fr 1.5fr;
        }
    }

    @media screen and (max-width: 1200px) {
        padding: 64px 0;
        
        &__container {
            display: flex;
            flex-direction: column;
        }

        &__left {
            margin: 0 auto;
            max-width: 300px;
            margin-top: 40px;
            margin-bottom: 80px;
        }
    }


}