.main {
    position: relative;
}

.main-back {
    &::before {
        content: '';
        display: block;
        background: url(../img/main-back.png);
        height: calc(100% - 200px);
        width: 181px;
        position: absolute;
        top: 200px;
        right: 40px;
        z-index: -1;
    }
}

