.tripple-swiper {
    &__top {
        display: flex;
        align-items: center;
        gap: 16px;
        justify-content: flex-end;
        margin-bottom: 30px;
    }

    &__info {

        &-current-number {
            position: absolute;
            left: 0;
            top: -60px;


            @media screen and (max-width: 619px) {
                top: -30px;
            }
        }

        &-size {
            position: absolute;
            right: 0;
            bottom: -60px;
            @media screen and (max-width: 619px) {
                bottom: -30px;
            }
        }

        &-numbers {
            width: 80px;
            font-size: 70px;
            color: #95744A;
            font-family: "Hram";
            position: relative;
            
            &::before {
                content: '';
                display: block;
                background: #95744A;
                width: 32px;
                height: 64px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                clip-path: polygon(80% 0, 100% 0, 20% 100%, 0 100%)
            }
            @media screen and (max-width: 619px) {
                width: 80px;
                font-size: 32px;

                &::before {
                    width: 16px;
                    height: 32px;
                }
            }
        }
    }
}