.prayer {
    padding: 240px 0 80px;

    @media screen and (max-width: 590px) {
        padding: 160px 0 80px;
    }

    &__ul {
        display: flex;
        flex-direction: column;
        gap: 18px;
        list-style-type: none;
        font-family: "Hram";
        padding: 0;
        margin: 0;
        margin-top: 24px;
    }

    &__item {
        background-color: #FDFDFD;
        border: solid 1px #C6C6C6;
        border-radius: 20px;
        padding: 16px 40px;
        gap: 24px;
        transition: background-color 0.3s;

        display: flex;
        align-items: center;

        &-left {
            margin-right: auto;
        }

        &-name {
            font-size: 30px;
        }

        &-description {
            opacity: 0.5;
            font-style: 22px;
            transition: opacity 0.3s, color 0.3s;
        }

        &-price {
            color: #BE8E51;
            font-size: 30px;
        }

        &-right {
            display: flex;
            align-items: center;
            gap: 24px;
        }
        &:hover {
            background: #F7F7F7;

            .prayer__item-description {
                color: black;
                opacity: 0.8;
            }
        }
        
    }

    @media screen and (max-width: 768px) {
        
        &__item {
            flex-direction: column;
            align-items: flex-end;
        }
    }

    @media screen and (max-width: 380px) {
        
        &__item {
            &-right {
                flex-direction: column;
                margin: 0 auto;
            }
        }
    }
}