.content {
    // display: grid;
    // grid-template-columns: 1fr;
    display: flex;
    flex-direction: column;
    gap: var(--gap);

    &__angle {
        margin-top: auto;
    }

    &__section {
        display: grid;
        // flex-direction: column;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;

        // grid-auto-flow: dense;

        &:nth-child(2n - 1) {
            .content__angle {
                transform: scaleX(-1);
            }
        }

        &-left {
            display: flex;
            flex-direction: column;
            align-items: start;
            height: 100%;
        }

        &:nth-child(2n) {
            .content__section-left {
                grid-column: 2;
                .content__angle {
                    margin-left: auto;
                }
            }
            .content__section-right {
                grid-row: 1;
            }
        }
    }

    @media screen and (max-width: 1152px) {
        &__section {
            display: flex;
            flex-direction: column;
        }

        // &__slider {
        //     width: 100% !important;
        // }

        // &
    }
}