.small-slider {
    width: calc(690 / 1440 * 100vw);

    @media screen and (max-width: 1152px) {
        width: auto;
    }
    overflow: hidden;
    position: relative;
    &__swiper {

        &-switch {
            position: absolute;
            bottom: 0;
            right: 0;
        }

        &-pagination {
            position: absolute;
            left: 12px !important;
            bottom: 65px !important;
            z-index: 20;
        }
    }

    &__slide {
        img {
            width: 100%;
            object-fit: fit;
        }
    }

    @media screen and (max-width: 1000px) {
        &__swiper-pagination {
            display: none;
        }
    }

    @media screen and (max-width: 768px) {
        &__swiper {
            width: calc(100vw - 60px);
            
        }
    }
}