.card {
    display: flex;
    flex-direction: column;
    gap: 32px;

    &__top {
        position: relative;

        .img {
            width: 100%;
            height: calc(450 / 1440 * 100vw);

            @media screen and (max-width: 980px) {

                height: calc(450 * 1.5 / 1440 * 100vw);
            }
            @media screen and (max-width: 620px) {

                height: calc(450 * 3 / 1440 * 100vw);
            }
        }
    }

    &__cross {
        left: 30px;
    }

    &__bottom {
        display: flex;
        justify-content: flex-end;
    }

    &__description {
        font-size: var(--description-fsz);
    }

    &__date {
        position: absolute;
        bottom: 0;
        left: 30px;
        transform: translateY(50%);
    }
}