.contacts-content {
    padding: 180px 0 60px;

    &__header {
        margin-bottom: 40px;
    }

    &__center {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: var(--gap);

        @media screen and (max-width: 950px) {
            grid-template-columns: 1fr;
        }
        &-left {
            display: flex;
            flex-direction: column;
            gap: 24px;
            position: relative;
        }

        &-title {
            color: #88653E;
            font-size: 28px;
            font-family: "Hram";
        }

        &-subtitle {
            font-size: var(--description-fsz);
        }

        &-right {
            img {
                width: 100%;
            }
        }
    }

    &__angle {
        position: absolute;
        bottom: 0;
        right: 0;

        @media screen and (max-width: 490px) {
            display: none;
        }
    }
}