.pages {
    display: flex;
    gap: 8px;

    &__item {
        font-size: 20px;
        font-family: "Hram";
        color: #717171;
        background: white;
        border: solid 1px #F5DABB;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 55px;
        height: 55px;
        cursor: pointer;
        &--active {
            background: #95744A;
            color: white;
        }
    }

}