.about-hram {
    padding: 180px 0 40px;

    &__header {
        margin-bottom: 60px;
    }

    &__center {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;

        &-left {
            position: relative;

            img {
                width: 100%;
                height: 100%;
            }
        }
        &-right {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: var(--gap);
        }
    }

    &__cross {
        left: 40px;
    }

    &__location {
        color: #BE9A6C;
        font-family: "Hram";
        font-size: 25px;
        padding-left: 40px;
        position: relative;

        &::before {
            position: absolute;
            width: 30px;
            height: 100%;
            background: currentColor;
            content: '';
            left: 0;
            clip-path: polygon(80% 0, 100% 0, 20% 100%, 0 100%);
        }
    }

    &__name {
        font-family: "Hram";
        color: var(--gold);
        margin-top: 40px;

        &-title {
            font-size: 320px;
            line-height: 240px;
            margin-left: -64px;
        }

        &-subtitle {
            font-size: 90px;
            max-width: 500px;
        }
    }

    &__right {
        &-bottom {
            display: flex;
            justify-content: space-between;
            align-items: end;
            padding-right: 40px;
        }
    }

    @media screen and (max-width: 1152px) {
        &__name {
            margin-top: 8px;
            &-title {
                font-size: 160px;
                margin: 0;
                line-height: 160px;
            }

            &-subtitle {
                font-size: 48px;
            }
        }

        &__location {
            font-size: 18px;
        }
    }

    @media screen and (max-width: 768px) {
        &__center {
            display: flex;
            flex-direction: column-reverse;

            &-right {
                margin-bottom: 40px;
            }
        }
        
    }
    
    
    @media screen and (max-width: 400px) {
        &__center-right {
            margin-bottom: 16px;
        }

        &__right {
            &-bottom {
                flex-direction: column-reverse;
                // margin-right: auto;
                padding-right: 0;
                align-items: center;
                margin-top: 80px;
                gap: 40px;
            }
        }

        &__name {
            margin-top: 8px;
            &-title {
                font-size: 32px;
                margin: 0;
                line-height: 48px;
            }

            &-subtitle {
                font-size: 32px;
            }
        }
        
    }
}